'use client';
import { createPost, fetchVideo } from '@/features/posts/post.server';
import { YoutubeOEmbed } from '@/features/videos/video.type';
import { useEffect, useState } from 'react';

export default function PostsPage() {
  const [url, setUrl] = useState('');
  const [video, setVideo] = useState<YoutubeOEmbed | null>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // コールバック関数
    if (!url) return;

    const run = async () => {
      try {
        const data = await fetchVideo(url);
        setVideo(data);
        setError(null);
      } catch (e) {
        if (e instanceof Error) {
          setError(e.message);
          setVideo(null);
        }
      }
    };
    run();
  }, [url]); // トリガー

  const handleSubmit = async (formData: FormData) => {
    try {
      const result = await createPost(formData);
    } catch (e) {
      setError(e.message);
    }
  };

  return (
    <div className="max-w-7xl mx-auto">
      <div>
        <form action={handleSubmit} className="flex flex-col space-y-5">
          <input
            name="url"
            value={url}
            onChange={(e) => {
              setUrl(e.target.value);
            }}
            className="bg-gray-300"
          ></input>
          <input name="text" className="bg-gray-300" />
          {video && (
            <div>
              <h2>{video.title}</h2>
              <h2>{video.author_name}</h2>
              <img src={video.thumbnail_url} />
            </div>
          )}
          <button>投稿</button>
        </form>
      </div>
      {error && <h2>{error}</h2>}
      {/* <h2>aaaaaa</h2> */}
    </div>
  );
}
