// 'use server';
import { YoutubeOEmbed } from '../videos/video.type';

export async function fetchVideo(videoUrl: string): Promise<YoutubeOEmbed> {
  const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/videos/preview`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ videoUrl }),
  });

  const data = await res.json();

  if (!res.ok) {
    throw new Error(data.message);
  }

  return data;
}

export async function createPost(formData: FormData) {
  const videoUrl = formData.get('url');
  const text = formData.get('text');

  console.log(videoUrl);
  console.log(text);

  const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/posts`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ videoUrl, text }),
  });

  const data = await res.json();
  console.log(res);
  console.log(data);

  if (!res.ok) {
    throw new Error(data.message);
  }

  console.log('登録OK');
  return data;
}
